@import '../../assets/css/theme';
.sun-empty {
  &-wrap {
    width: 100%;
    min-height: 213px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: $color-card-bg-active;
    border-radius: 10px !important;
    margin-bottom: 30px;
  }
  &-img {
    height: 120px;
    width: auto;
    margin-bottom: 8px;
    opacity: 0.5;
  }
  &-text {
    font-size: 12px;
    font-weight: 400;
    color: $color-text-default-5;
    line-height: 17px;
  }
  &-btn {
    padding: 0 22px;
    height: 29px;
    line-height: 29px;
    text-align: center;
    background: $color-button-primary;
    border-radius: 15px;
    font-size: 12px;
    font-weight: 600;
    color: #fff;
    margin-top: 16px;
    cursor: pointer;
  }
}

@media screen and (max-width: 768px) {
  .sun-empty-wrap {
    .sun-empty-text.sun-empty-btn {
      font-size: 14px;
    }
    img {
      height: 60px;
      width: auto;
    }
  }
}
