@import '../../assets/css/theme';
@import '../../assets/css/mixins';

.m_lang_select_wrap {
  border-bottom: 1px solid rgba(219, 202, 255, 0.15);
  .m_lang_select {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px 0 10px;
    cursor: pointer;
    .m_lang_select_text {
      font-family: Poppins;
      font-weight: 600;
      font-size: 16px;
      line-height: 30px;
      color: rgba(158, 154, 188, 1);
    }
    .m_lang_select_icon {
      margin-left: auto;
      width: 14px;
      height: 14px;
      transition: all 0.3s;
      transform: rotate(90deg);
      background: url(../../assets/images/menus/view_arrow.svg) no-repeat 0 0 / 100% 100%;
    }
  }
  .m_pop_lang_select {
    margin-bottom: 20px;
  }
  .m_pop_lang_select_item {
    padding: 10px 0;
    font-family: PingFang SC;
    font-weight: 600;
    font-size: 16px;
    line-height: 22px;
    color: #a7a2c9;
    display: flex;
    align-items: center;
    justify-content: space-between;
    img {
      display: none;
    }
  }
  .m_pop_lang_select_active {
    color: #fff;
    img {
      display: block;
    }
  }
}
.m_lang_select_wrap_active {
  .m_lang_select {
    .m_lang_select_text {
      color: #fff;
    }

    .m_lang_select_icon {
      transform: rotate(270deg);
    }
  }
}
