.glow-button {
  position: relative;
  padding: 1px;
  width: 100%;
  height: 50px;
  //background: rgba(39, 25, 61, 1);
  background: linear-gradient(180deg, #0D0616 11.25%, #1D0A35 91.25%);
  border: 1px solid #6400FF;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50px;
  overflow: hidden;
  box-shadow: 0px -16px 19px 0px #8333FF24 inset;

  .glow-button-inner {
    z-index: 2;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 48px;
    border-radius: 48px;
    background: rgba(39, 25, 61, 1);
  }

  .glow-effect {
    will-change: transform, offset-path; /* 提前告知浏览器 */
    transform: translateZ(0); /* 强制GPU加速 */
    backface-visibility: hidden;
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 60px;
    height: 60px;
    background: url(../../assets/images/header/btn_light_shadow.svg) no-repeat center center;
    border-radius: 50%;
    animation: move-along-border 4s linear infinite;
    z-index: 0;
  }
}

.glow-button-active {
  //background: rgba(50, 35, 83, 1);
  //border: 1px solid rgba(255, 255, 255, 0.2);
  border: 1px solid #682DC2;
  box-shadow: 0px -16px 19px 0px #8333FF3D inset;
  .glow-button-inner {
    background: rgba(50, 35, 83, 1);
  }
  .glow-effect {
    display: none;
  }
}
.glow-button-disabled {
  opacity: 0.3;
  cursor: no-drop;
}

@keyframes move-along-border {
  0% {
    offset-distance: 0%;
  }
  100% {
    offset-distance: 100%;
  }
}
