.shiny_text {
  display: inline-block;
  font-weight: bold;

  /* 文字透明 + 背景裁剪 */
  background-clip: text;
  -webkit-background-clip: text;
  color: transparent;

  background-size: 200% auto;
  animation: shiny linear infinite;

  @keyframes shiny {
    0% {
      background-position: 0% center;
    }
    100% {
      background-position: -200% center;
    }
  }
}
