#root .top-menu-bar {
  z-index: 10;
  display: flex;
  justify-content: space-between;
  padding: 18px 0;
  position: relative;
  margin: 0 auto;
  @include common-content-width;

  .menu-has-icon {
    position: relative;
  }
  .menu-right-corner-icon {
    position: absolute;
    right: -10px;
    top: -20px;
    z-index: 1;
    width: 32px;
    height: 20px;
  }
  .menu-left-tip-icon {
    margin-right: 4px;
  }
  .menu-right-tip-icon {
    position: absolute;
    right: 0;
    top: 0;
    transform: translate(-5px, -100%);
  }
}

#root .top-menu-bar .inner-box {
  font-weight: 500;
  text-align: center;
  display: flex;
  justify-content: space-between;
  align-items: center;
  //height: 34px;
  flex-shrink: 0;

  //& > * {
  //  &:not(:last-child) {
  //    margin-right: 10px;
  //  }
  //}

  button {
    height: 70px;
    font-size: 16px;
    background: transparent;
    margin: 0;
    padding: 0;
    min-width: 0px !important;

    &:hover {
      a {
        color: $color-text-default !important;
        opacity: 1 !important;
      }
    }
  }

  .sun-logo {
    height: 32px;
    margin-right: 50px;
  }

  .header-list-item a {
    position: relative;
    margin: 0;
    text-decoration: none;
    color: $color-text-default-5;
    padding: 0 15px;
    display: inline-flex;
    align-items: center;
    font-size: 16px;
    line-height: 22px;
    height: 22px;

    &.is-hover-purple.active {
      color: $color-text-default !important;
      opacity: 1 !important;
      &.menu-has-sub {
        &::after {
          margin-left: -18px;
        }
      }
      &::after {
        content: '';
        position: absolute;
        left: 50%;
        margin-left: -10px;
        bottom: -10px;
        width: 20px;
        height: 4px;
        background: #722fff;
        border-radius: 2px;
      }
    }

    &.disabled {
      color: #675d72 !important;
      cursor: not-allowed;
    }

    &.menu-title {
      pointer-events: none;
      margin-left: 0;
      // padding: 5px;
      // background: red;

      &:hover {
        background: #5915e1;
        color: #fff !important;
      }
    }

    &.new,
    &.hot {
      margin-right: -18px;
    }

    &.is-hover-purple:hover {
      color: $color-text-default !important;
      opacity: 1 !important;
    }
  }

  .top-list a {
    color: #333;
    height: 20px;

    &.active {
      color: #5915e1;
    }
  }

  .top-list .selected a,
  .full-list .selected a {
    color: #fff;
  }

  .full-list {
    a {
      color: #333;
      font-weight: normal;
    }
  }

  .address-text {
    color: #5915e1;
    font-weight: normal;
  }

  .menu-has-sub {
    padding-right: 30px !important;
    cursor: default;
    .menu-has-sub-menu-icon {
      position: absolute;
      right: 15px;
      top: 50%;
      margin-top: -4px;
      opacity: 0.7;
    }
    &:hover {
      .menu-has-sub-wrap {
        display: block;
      }
      .menu-has-sub-menu-icon {
        opacity: 1;
      }
    }
    .menu-has-sub-wrap {
      z-index: 3;
      display: none;
      position: absolute;
      left: 15px;
      bottom: 0;
      transform: translateY(100%);
      padding-top: 20px;
      white-space: nowrap;
      .menu-has-sub-menu-list {
        //padding: 12px 0;
        background: #4d4366;
        border-radius: 5px;
        overflow: hidden;
        box-shadow: 0px 0px 6px 0px rgba(37, 31, 50, 0.3);
        @include lightThemeOnly {
          background: #fff;
          box-shadow: 0px 0px 6px 0px rgba(37, 31, 50, 0.1);
        }
      }
      .menu-has-sub-menu-row {
        font-weight: 600;
        font-size: 16px;
        color: $color-text-default-7;
        line-height: 58px;
        font-style: normal;
        text-align: left;
        cursor: pointer;
        height: 58px;
        border-radius: 5px;
        padding: 0 30px 0 24px;
        box-sizing: content-box;
        &:first-child {
          margin-top: 0;
        }
        &:hover {
          color: $color-text-default !important;
          background: rgba(103, 86, 146, 1);
          @include lightThemeOnly {
            background: #f5f5f5;
          }
        }
        //&.menu-has-sub-menu-row-active {
        //  color: $color-text-default !important;
        //  background: rgba(103, 86, 146, 1);
        //  @include lightThemeOnly {
        //    background: #f5f5f5;
        //  }
        //}

        .menu-has-sub-menu-row-tag {
          display: inline-block;
          margin-left: 6px;
          padding: 0 7px;
          line-height: 17px;
          border-radius: 17px;
          background: #decdff;
          white-space: nowrap;
          font-family: PingFangSC, PingFang SC;
          font-weight: 600;
          font-size: 12px;
          color: #8c51ff;
        }
      }
    }
  }
}

