.banner {
  width: 750px;
  // height: 172px;
  // margin: 20px auto 57px;
  margin: 20px auto 40px;
  text-align: center;
  overflow: hidden;
  // background: url(../images/banner.png) 0 0 no-repeat;
  // background-size: 100% 100%;

  .banner-title {
    margin-top: 48px;
    font-size: 36px;
    line-height: 30px;
    letter-spacing: 0.833333px;
    color: #ffffff;
    font-weight: 500;

    &.sp {
      font-size: 24px;
      line-height: 34px;
    }
  }

  .banner-des {
    margin-top: 16px;
    font-weight: normal;
    font-size: 16px;
    line-height: 32px;
    letter-spacing: 0.833333px;
    color: #ffffff;
  }

  .banner-tvl {
    color: #fff;
    margin: 30px 0 20px;

    .tvl-value {
      font-weight: bold;
      font-size: 50px;
      line-height: 68px;
      text-align: center;
      letter-spacing: 2px;
    }

    .tvl-title {
      font-weight: 500;
      font-size: 12px;
      line-height: 16px;
      text-align: center;
      color: #999;
    }
  }

  .banner-tvl-detail {
    display: flex;
    justify-content: space-around;
    margin: 15px 0 20px;

    .tvl-value {
      font-weight: bold;
      font-size: 20px;
      line-height: 27px;
      text-align: center;
      letter-spacing: 2px;
      color: #fff;
    }

    .tvl-title {
      font-weight: 500;
      font-size: 12px;
      line-height: 16px;
      text-align: center;
      color: #999;
    }
  }

  .banner-audit {
    display: inline-block;
    background-color: rgba(255, 255, 255, 0.1);
    border-radius: 5px;
    font-weight: normal;
    font-size: 14px;
    line-height: 32px;
    letter-spacing: -0.4px;
    color: #ffffff;
    padding: 4px 10px;
    margin: 16px auto 0;

    a {
      color: #ded0f9;
      text-decoration: underline;
    }
  }

  &.pool-config {
    width: 100%;
    max-width: 1200px;

    .banner-title,
    .banner-des {
      text-align: left;
    }

    .banner-title {
      display: flex;
      align-items: center;
      font-weight: 600;
      font-size: 40px;
      line-height: 64px;
      color: #ffffff;

      > img {
        width: 36px;
        height: 36px;
        margin-left: 20px;
      }
    }

    .banner-des {
      font-size: 14px;
      line-height: 20px;
      color: rgba(255, 255, 255, 0.7);
      margin-top: 8px;

      a {
        color: #fff;
        text-decoration: underline;

        &:hover {
          color: #ded0f9;
        }
      }
    }
  }
}

