    border-radius: 5px;
    background: rgba(255, 255, 255, 0.1);
  }

  .banner {
    .banner-tvl {
      display: flex;
      flex-direction: column-reverse;
      margin-top: 0;

      .tvl-value {
        margin-top: 4px;
      }
    }

    .banner-tvl-detail {
      display: block;
      margin-bottom: 0;

      > div {
        display: flex;
        justify-content: space-between;
        flex-direction: row-reverse;
        align-items: center;
        margin-bottom: 10px;

        &:last-child {
          margin-bottom: 0;
        }
      }

      .tvl-title {
        font-size: 14px;
        line-height: 14px;
        color: #ffffff;
      }

      .tvl-value > span {
        font-weight: bold;
        font-size: 14px;
        line-height: 15px;
        color: #ffffff;
      }
    }
  }
}

