}

.sun-io-copy-text {
  width: 108px;
  height: 40px;
  background: rgba(17, 17, 17, 0.5);
  border-radius: 5px;
  font-size: 14px;
  font-weight: 400;
  color: #ffffff;
  line-height: 40px;
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  z-index: 11000;
  text-align: center;
}

.wallet-modal {
  .step2-wallet {
    display: flex;
    align-items: center;
    justify-content: center;
    //padding-top: 30px;
    font-size: 20px;
    font-family: Poppins;
    font-weight: 500;


    .step2-wallet-logo {
      width: 20px;
      height: 20px;
      margin-right: 8px;
      img {
        display: block;
        width: 100%;
        height: 100%;
      }
    }
  }
  .loading-img {
    @keyframes _loading {
      100% {
        transform: rotate(360deg);
      }
    }
    display: block;
    margin: 28px auto 30px;
    width: 70px;
    text-align: center;
    animation: loadingCircle 1s infinite linear;
  }
  .login-tip-text {
    text-align: center;
    font-size: 12px;
    font-family: Poppins;
    font-weight: 500;
    color: #A7A2C9;

    a {
      color: #A572FF;
      text-decoration: underline;
      &:hover {
        color: #C1A0FF;
      }
    }
  }

  .init-text {
    font-size: 17px;
    margin-top: 25px;
    text-align: center;
    font-weight: 600;
  }

  @include mobile-only {
    .step2-wallet {
      padding-top: 10px;
      .step2-wallet-logo {
        margin-right: 10px;
        width: 30px;
        height: 30px;
      }
    }
    .loading-img {
      margin-top: 20px;
    }
    .init-text {
      margin-top: 10px;
      font-size: 14px;
    }
    .login-tip-text {
      margin-top: 24px;
    }
  }
}

@media screen and (max-width: 1200px) {
  .if-mobile-hide {
    display: none;
  }
  #root .top-menu-bar {
    padding: 18px 10px;
  }
}

@media screen and (max-width: 575px) {
  .extension-from .inline-form {
    .ant-form-item-label {
      flex: initial;
    }
    .ant-form-item-control {
      flex: initial;
    }
  }
}
.wallet_login_conflicts {
