@import '../../../assets/css/mixins';
@import '../../../assets/css/theme';

.m-header-list-item {
  border-bottom: 1px solid rgba(219, 202, 255, 0.15);
  .m-header-main-item {
    position: relative;
    display: flex;
    align-items: center;
    padding: 24px 0 10px;

    .m-header-main-item-text {
      font-family: Poppins;
      font-weight: 600;
      font-size: 16px;
      height: 30px;
      line-height: 30px;
      color: rgba(158, 154, 188, 1);
      user-select: none;
    }

    .m-header-main-item-icon {
      display: block;
      margin-left: auto;
      width: 11px;
      height: auto;
      transition: all 0.3s;
      //transform: rotate(90deg);
      color: rgba(162, 156, 188, 1);
      //img {
      //  display: block;
      //  width: 100%;
      //  height: 100%;
      //}
    }
  }
  .m-header-sub-item {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
    &:last-child {
      margin-bottom: 20px;
    }
    .m-header-sub-text {
      position: relative;
      margin: 0 10px;
      font-family: PingFangSC-Regular;
      font-weight: 600;
      font-size: 20px;
      color: $color-text-default;
      line-height: 28px;
      user-select: none;
    }
    .m-header-sub-icon {
      margin-right: 12px;
      width: 30px;
      height: 30px;
      img {
        width: 100%;
        height: 100%;
      }
      .m-header-sub-icon-default {
        display: block;
      }
      .m-header-sub-icon-active {
        display: none;
      }
    }
    .m-header-sub-main {
      flex: 1;
      .m-header-sub-name {
        display: inline-flex;
        align-items: center;
        font-family: Poppins;
        font-weight: 600;
        font-size: 14px;
        line-height: 21px;
        color: #fff;
        .m-header-sub-name-icon {
          margin-left: 6px;
          width: 14px;
          height: 14px;
          background: url(../../../assets/images/header/ico_link.svg) no-repeat no-repeat 0 0 / 100% 100%;
        }
      }
      .m-header-sub-tag {
        height: 14px;
        margin-left: 6px;
      }
      .m-header-sub-desc {
        font-family: Poppins;
        font-weight: 400;
        font-size: 12px;
        line-height: 18px;
        color: rgba(158, 154, 188, 1);
      }
    }
  }
  .m-header-sub-item-active {
    .m-header-sub-text {
      color: rgba(191, 171, 255, 1);
    }
    .m-header-sub-icon {
      .m-header-sub-icon-default {
        display: none;
      }
      .m-header-sub-icon-active {
        display: block;
      }
    }
    .m-header-sub-main {
      .m-header-sub-name {
        color: #fff;
        .m-header-sub-name-icon {
          background-image: url(../../../assets/images/header/ico_link_active.svg);
        }
      }
      .m-header-sub-desc {
        color: #fff;
      }
    }
  }
}
.m-header-sub-list {
  display: none;
  margin-top: 10px;
}
