.m-header-list-item-active {
  .m-header-main-item {
    .m-header-main-item-text {
      color: #fff;
    }
  }
}
.m-header-list-item-subshow {
  .m-header-main-item {
    .m-header-main-item-text {
      color: #fff;
    }
    .m-header-main-item-icon {
      transform: rotate(180deg);
    }
  }
  .m-header-sub-list {
    display: block;
  }
}
