// 消息中心样式
.account-notice-center {
  color: $color-text-default-7;
  .account-notice-list {
    max-height: 400px;
    overflow-y: auto;
    .account-notice-item {
      padding: 10px 20px;
      cursor: pointer;
      margin: 0 10px;
      &:hover {
        background: $list-hover-color-bg;
        border-radius: 6px;
      }

      &:last-child {
        border: 0;
      }
      .account-notice-title {
        display: flex;
        align-items: center;
        margin-bottom: 10px;

        .account-notice-title-info {
          display: flex;
          flex: 1;
          align-items: center;
          .account-notice-icon {
            display: flex;
            opacity: 0.5;
            img {
              display: block;
            }
          }
          .account-notice-title-name {
            display: flex;
            align-items: center;
            margin-right: 10px;
            font-size: 16px;
            font-weight: 600;
            color: $color-text-default-4;
            line-height: 22px;
          }
          .account-notice-icon-new {
            display: block;
            height: 20px;
            width: auto;
          }
          .account-notice-fee {
            margin-left: 10px;
            margin-right: 10px;
            font-size: 12px;
            color: $list-text-color-secondary;
          }
          .account-notice-version {
            padding: 0 10px;
            font-size: 12px;
            font-weight: 600;
            line-height: 18px;
            text-align: center;
            color: #C1A0FF;
            border: 1px solid #C1A0FF80;
            border-radius: 20px;
          }
        }
        .account-notice-time {
          font-size: 12px;
          font-weight: 400;
          line-height: 17px;
          color: $color-text-default-3;
        }
      }

      .account-list-content {
        font-size: 14px;
        font-weight: 400;
        line-height: 22px;
        color: $color-text-default-3;
      }
    }

    .account-notice-item-new {
      .account-notice-title {
        .account-notice-title-info {
          .account-notice-icon {
            opacity: 1;
          }
          .account-notice-title-name {
            color: $color-text-default;
          }
          .account-notice-fee {
            color: $list-text-color-secondary;
            font-weight: 400;
          }
          .account-notice-version {
            opacity: 1;
          }
        }
        .account-notice-time {
          color: $color-text-default-7;
        }
      }
      .account-list-content {
        color: $color-text-default;
        font-family: PingFangSC-Regular, PingFang SC;
        font-weight: 400;
      }
    }
  }
}

