//  --table-hover-radius: 6px;
//}

.trade-list-table .ant-table-tbody tr.ant-table-row {
  position: relative;
  z-index: 1;
  --table-width: 560px;
  --table-hover-padding: 20px;
  --table-hover-bg: rgba(63, 46, 110, .3);
  --table-hover-radius: 6px;

  td {
    border-bottom: none !important;
  }
  &:hover td {
    background: transparent;
  }
  &:hover td:first-child::before {
    position: absolute;
    content: "";
    left: calc(-1 * var(--table-hover-padding));
    width: calc(var(--table-width) + var(--table-hover-padding) + var(--table-hover-padding));
    background: var(--table-hover-bg);
    display:  block;
    top: 0;
    bottom: 0;
    z-index: -1;
    border-radius: var(--table-hover-radius);
  }
}
.tab-type-notice.trade-list-tabs {
  margin: 0;
  .dark-tabs-header {
