$active_icon_color: rgba(174, 119, 255, 1);
$disabled_icon_color: rgba(255, 255, 255, 0.3);

.page {
  text-align: center;
  .page_icon {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    display: block;
  }
}
// 调整pre next 按钮
.page :global(.ant-pagination-prev) {
  position: relative;
  transform: rotate(180deg);
  color: $active_icon_color;
}
.page :global(.ant-pagination-next) {
  position: relative;
  color: $active_icon_color;
  // 抵消最后一个页码的margin-right
  margin-left: -10px !important;
}

.page :global(.ant-pagination-prev.ant-pagination-disabled),
.page :global(.ant-pagination-next.ant-pagination-disabled) {
  color: $disabled_icon_color;
}

// 调整非选中态页码按钮
.page :global(.ant-pagination-item) {
  height: 18px !important;
  line-height: 16px !important;
  margin: 0 10px 0 0 !important;
  min-width: 0 !important;
  a {
    padding: 0 5px;
    font-size: 12px;
  }
}
.page :global(.ant-pagination-item:not(.ant-pagination-item-active)) {
  border: 1px solid #fff !important;
  opacity: 0.5;
  a {
    color: #fff;
  }
  &:hover {
    opacity: 1;
  }
}
.page :global(.ant-pagination-item.ant-pagination-item-active) {
  border: 1px solid #6400ff !important;
  a {
    color: #fff;
  }
}

// 调整省略号按钮
.page :global(.ant-pagination-jump-next.ant-pagination-jump-next-custom-icon),
.page :global(.ant-pagination-jump-prev.ant-pagination-jump-prev-custom-icon) {
  height: 18px !important;
  width: 18px !important;
  line-height: 16px !important;
  margin: 0 10px 0 0 !important;
  min-width: 0 !important;
  font-size: 12px;
}
