@import '../../../../../assets/css/theme';
@import '../../../../../assets/css/mixins';

$close: #cccccc;

.v3-text-range {
  font-size: 14px;
  font-family: Poppins;
  font-weight: 600;
  line-height: 21px;
  cursor: pointer;
  > span {
    display: inline-block;
    width: 4px;
    height: 4px;
    border-radius: 100%;
    margin-right: 6px;
    vertical-align: middle;
  }
  &.close {
    color: $close;
    > span {
      background: $close;
    }
  }
  &.in {
    color: #00ff96;
    > span {
      background: #00ff96;
    }
  }
  &.out {
    color: $color-warning-text;
    > span {
      background: $color-warning-text;
    }
  }
}

.v3-range-badge {
  border-radius: 0px 10px 0px 10px;
  position: absolute;
  right: 0;
  top: 0;
  padding: 4px 15px;
  color: #ffffff;
  cursor: pointer;
  &.close {
    background: $close;
  }
  &.in {
    background: $color-safe-text;
  }
  &.out {
    background: $color-warning-text;
  }
}

@include mobile-only {
  .v3-text-range {
    font-size: 12px;
    line-height: 18px;
  }
}
