@import '../../../assets/css/mixins';
@import '../../../assets/css/theme';

.mobile-drawer-main {
  height: 100%;
  overflow-y: scroll;
}
.m-header-list {
  padding: 0 20px;
  height: calc(100% - 80px);
  overflow: auto;
}

.m-header-mode {
  display: flex;
  align-items: center;
  padding: 20px 0;
  .m-header-mode-text {
    font-family: Poppins;
    font-weight: 600;
    font-size: 20px;
    line-height: 30px;
    color: rgba(158, 154, 188, 1);
    user-select: none;
  }
  .m-header-mode-icon2 {
    margin-left: auto;
    width: 46px;
    height: 24px;
    img {
      display: block;
      width: 100%;
      height: 100%;
    }
  }
}
