@import "../../assets/css/mixins";

.wallet_list {
  display: grid;
  grid-template-columns: repeat(4, 1fr); /* 4 列等宽 */
  grid-template-rows: repeat(2, auto);   /* 2 行高度由内容决定，或用固定高度 */
  gap: 0; /* 行列间距 */
  align-items: stretch; /* 子项等高 */
}
.wallet_item {
  //width: 140px;
  height: 140px;
  text-align: center;
  padding: 20px 0;
  color: #A7A2C9;
  font-family: Poppins;
  font-weight: 500;
  font-size: 14px;
  line-height: 18px;
  border-radius: 20px;
  cursor: pointer;
  img {
    border-radius: 10px;
  }
  &:hover {
    background: #28154A;
  }
}
.wallet_icon {
  width: 64px;
  height: 64px;
  margin-bottom: 16px;
}
:global(.wallet-modal.ant-modal .ant-modal-content), :global(.wallet-modal.ant-modal .ant-modal-header) {
  background: #21103D;
}
.wallet_modal:global(.dark-modal.ant-modal) :global(.ant-modal-header) {
  padding-bottom: 0;
}
.wallet_modal_title {
  font-size: 24px;
}
@include mobile-only {
  .wallet_list {
    grid-template-columns: repeat(3, 1fr); /* 4 列等宽 */
    grid-template-rows: repeat(3, auto);   /* 2 行高度由内容决定，或用固定高度 */
    .wallet_item {
      height: 80px;
      font-size: 12px;
      padding: 14px 0;
      .wallet_icon {
        width: 24px;
        height: 24px;
        border-radius: 4px;
        margin-bottom: 10px;
      }
    }
  }
  .wallet_modal_title {
    font-size: 18px;
  }
}