@import '../../assets/css/theme';
@import '../../assets/css/mixins';

.menu-hover-dropdown {
  position: relative;
  cursor: pointer;
  font-size: 0;
  &:hover {
    .menu-hover-dropdown-global {
      opacity: 1;
    }
    .header-select-box {
      display: block;
    }
    .header-icon-inner {
      background: url(../../assets/images/header/lang_bg_hover.svg) no-repeat center center;
    }
  }

  .header-icon-inner {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 40px;
    height: 40px;
    border-radius: 40px;
    //background: rgba(39, 25, 61, 1);
    //background: #0D0616;
    cursor: pointer;
    background: url(../../assets/images/header/lang_bg.svg) no-repeat center center;
    //border: 1px solid #FFFFFF33;
    //box-shadow: 0px -16px 19px 0px #8333FF24 inset;
  }

  .menu-hover-dropdown-global {
    display: block;
    width: 18px;
    height: 18px;
  }

  .header-select-box {
    display: none;
    z-index: 3;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    width: 132px;
    font-size: 16px;
    overflow: hidden;
    .lang-wrap {
      width: 132px;
      text-align: center;
    }

    .header-wrapper {
      //padding: 10px 0;
      //background: #4d4366;
      //border-radius: 10px;
      //border: 1px solid rgba(255, 255, 255, 0.2);
      //background: rgba(38, 25, 61, 1);
      //overflow: hidden;
      border: 1px solid rgba(255, 255, 255, 0.2);
      background: #1B0D3180;
      border-radius: 10px;
      padding: 10px;
      backdrop-filter: blur(26px);
      transform: translateZ(0)
    }

    .h15 {
      height: 13px;
    }
    .top-list {
      color: #9E9ABC;
      font-weight: 600;
      li {
        margin-bottom: 10px;
        padding: 0 10px;
        height: 40px;
        line-height: 40px !important;
        border-radius: 6px;
        font-family: PingFang SC;
        font-size: 14px;
        font-weight: 600;
        cursor: pointer;
        //white-space: nowrap;
        display: flex;
        align-items: center;
        justify-content: space-between;
        img {
          margin-right: 10px;
        }
        &:last-child {
          margin-bottom: 0;
        }
      }
    }

    li.selected,
    li:hover {
      background: rgba(#4C3079, .5);
    }
    li.disabled {
      cursor: default;
      color: #fff;
      background: none;
    }
  }
}
