.drop_down:global(.ant-dropdown) {
  color: #fff;
}
.drop_down :global(.ant-dropdown-menu) {
  padding: 10px 6px 10px 10px;
  list-style-type: none;
  background: rgba(27, 13, 49, 0.5);
  border-radius: 10px;
  border: 1px solid rgba(68, 53, 100, 1);
  outline: none;
  backdrop-filter: blur(50px);
  width: 128px;
}
.drop_down :global(.ant-dropdown-menu .ant-dropdown-menu-item) {
  padding: 0;
}
.item {
  margin: 0 auto;
  width: 112px;
  height: 40px;
  line-height: 40px;
  border-radius: 6px;
  font-family: PingFang SC;
  font-weight: 600;
  font-size: 14px;
  color: rgba(158, 154, 188, 1);
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-left: 10px;
  &:active {
    background: rgba(158, 154, 188, .5);
    color: rgba(158, 154, 188, 1);

  }
  &_active {
    background: transparent;
    color: #fff;
    &:active {
      background: transparent;
    }
  }
}
.symbol {
  margin-right: 10px;
  width: 10px;
}
.anchor {
  background: url(../../assets/images/langSelect/bg.png) no-repeat center / contain;
  width: 128px;
  height: 32px;
  display: flex;
  align-items: center;
  padding: 0 10px;
  color: #fff;
  font-family: Poppins;
  font-weight: 500;
  font-size: 14px;
  margin-right: 20px;
  &:hover {
    background: url(../../assets/images/langSelect/bg_active.png) no-repeat center / contain;
  }
}
.icon1 {
  margin-right: 10px;
  width: 16px;
  height: 16px;
}
.icon2 {
  margin-left: auto;
  color: rgba(162, 156, 188, 1);
}
.open {
  .icon2 {
    color: #fff;
    transform: rotate(180deg);
    transition: all 200ms ease-in-out;
  }
}