@import '../../assets/css/mixins';
.theme_btn_pc {
  display: inline-block;
  width: 22px;
  height: 22px;
  background-size: contain;
  cursor: pointer;
  margin-right: 30px;
  opacity: 0.6;
  &:hover {
    opacity: 1;
  }
  @include darkThemeOnly {
    background-image: url('../../assets/images/light_icon.png');
  }
  @include lightThemeOnly {
    background-image: url('../../assets/images/dark_icon.png');
  }
}
