@import 'theme';
@import 'mixins';
.ant-notification .sun-notify {
  width: 480px !important;
  background: #26193d;
  padding: 16px 20px !important;
  border-radius: 10px;
  overflow: visible;
  .ant-notification-notice-message {
    display: none;
  }
  .ant-notification-notice-close {
    top: 10px !important;
    right: 10px !important;
    color: #fff !important;
    &:hover {
      opacity: 0.7;
    }
  }
}

// 交易弹窗
.sun-notify {
  .typo-text-link {
    margin-left: 0;
    @include responseFont();
  }
}

@include mobile-only {
  .ant-notification .sun-notify {
  }
}
