.link {
  position: relative;
  //padding: 0 30px;
  cursor: pointer;
  &:hover {
    .link_inner {
      span {
        color: #fff;
      }
      .menu_icon {
        color: #fff;
        transform: rotate(-180deg);
      }
    }
    .drop {
      display: block;
    }
  }
  &.link_active {
    .link_inner {
      span {
        color: #fff;
      }
      .menu_icon {
        color: #fff;
      }
    }
  }
  .link_inner {
    display: flex;
    align-items: center;
    font-family: Poppins;
    font-weight: 500;
    font-size: 18px;
    line-height: 28px;
    span {
      position: relative;
      margin-right: 6px;
      color: #A29CBC;
      transition: all 0.3s;
    }
    .menu_icon {
      display: block;
      margin-left: 4px;
      width: 14px;
      height: 14px;
      color: #A29CBC;
      transition: all 0.3s;
    }
  }

  // drop
  .drop {
    z-index: 5;
    display: none;
    position: absolute;
    left: 50%;
    top: 25px;
    transform: translateX(-50%);
    //&::after {
    //  content: '';
    //  position: absolute;
    //  inset: 0;
    //  backdrop-filter: blur(30px);
    //  background: #1B0D3180;
    //  z-index: -1;
    //  top: 0;
    //  left: 0;
    //  bottom: 0;
    //  right: 0;
    //}
    .stand {
      height: 29px;
    }
    .list {
      padding: 10px;
      border: 1px solid #443564;
      border-radius: 10px;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        left: 0;
        right: 0;
        bottom: 0;
        top: 0;
        z-index: -1;
        backdrop-filter: blur(26px);
        background: #1B0D3180;
        transform: translateZ(0);
        border-radius: 10px;
      }
    }
    .list_normal {
      .normal_item {
        display: flex;
        align-items: center;
        padding: 12px 10px 10px;
        position: relative;
        .name {
          font-weight: 600;
          font-size: 14px;
          line-height: 21px;
          color: #fff;
        }
        &:last-child {
          margin-bottom: 0;
        }
        &:hover,
        &.normal_item_active {
          background: rgba(#372358, .5);
          border-radius: 6px;
          .normal_img_default {
            display: none;
          }
          .normal_img_active {
            display: block;
          }
          //.name {
          //  color: rgba(191, 171, 255, 1);
          //}
          .name_img_default {
            display: none;
          }
          .name_img_active {
            display: block;
          }
          .desc {
            color: #fff;
          }
        }
        &:hover {
          .desc {
            color: #9E9ABC;
          }
        }
        &.normal_item_active {
          background: none;
          .desc {
            color: #fff;
          }
        }
      }
      .normal_img {
        display: block;
        margin-right: 12px;
        width: 30px;
        height: 30px;
        border-radius: 5px;
        img {
          width: 100%;
          height: 100%;
        }
      }
      .normal_img_default {
        display: block;
      }
      .normal_img_active {
        display: none;
      }

      .normal_main {
        flex: 1;
        white-space: nowrap;
      }
      .name {
        display: inline-flex;
        align-items: center;
        font-family: Poppins;
        font-weight: 600;
        font-size: 16px;
        line-height: 24px;
        color: #fff;
        transition: all 0.3s;
        .name_tag {
          margin-left: 6px;
          height: 14px;
        }
        .name_img {
          margin-left: 6px;
          width: 14px;
          height: 14px;
          img {
            width: 100%;
            height: 100%;
          }
        }
        .name_img_default {
          display: block;
        }
        .name_img_active {
          display: none;
        }
      }
      .desc {
        font-family: Poppins;
        font-weight: 400;
        font-size: 12px;
        line-height: 18px;
        color: rgba(158, 154, 188, 1);
        transition: all 0.3s;
      }
    }

