@import '../../../assets/css/var.scss';
@import '../../../assets/css/mixins';

.pc_header_wrap {
  z-index: 100;
  &:after {
    content: '';
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    z-index: -1;
    backdrop-filter: blur(26px);
    background: transparent;
    transform: translateZ(0)
  }
}
.pc_header {
  z-index: 10;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  margin: 0 auto;
  min-width: 1200px;
  max-width: 1920px;
  width: 100%;
  height: 64px;
  padding: 0 120px;

  .logo {
    img {
      display: block;
      height: 40px;
    }
  }

  .tools {
    display: flex;
    align-items: center;
  }
  .lang {
    margin-right: 21px;
  }

  &:after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 1px; /* 下边框高度 */
    background: linear-gradient(90deg, rgba(108, 102, 134, 0.2) 15%, #6C6686 50.38%, rgba(108, 102, 134, 0.2) 85%);
    opacity: 0.3;
    transform: translateZ(0);
  }
}

@include pc-small-screen {
  .pc_header_wrap {
    .pc_header {
      padding: 0 60px;
      //.logo {
      //}
      //.tools {
      //}
      //.lang {
      //}
    }
  }
}