@import '../../assets/css/var';
.header-toast {
  z-index: 10;
  background: rgba(29, 22, 44, 1);
  font-weight: 500;
  font-size: 14px;
  line-height: 22px;
  color: #ffffff;
  text-align: center;
  position: relative;
  height: 40px;
  padding: 0 14px;
  font-family: Poppins;

  .white-close {
    display: block;
    position: absolute;
    right: 20px;
    top: 0;
    bottom: 0;
    margin: auto;
    width: 10px;
    height: 10px;
    cursor: pointer;
    color: rgba(153, 153, 153, 1);
  }

  .toast-text {
    display: inline-block;
    height: 40px;
    overflow: hidden;

    ul {
      display: block;
      height: 40px;
      line-height: 22px;
      margin: 0;
      position: relative;

      &.animate {
        -webkit-animation: 7.5s rowup linear infinite normal;
        animation: 7.5s rowup linear infinite normal;
      }
    }

    li {
      display: flex;
      align-items: center;
      justify-content: center;
      height: 40px;
      text-align: center;
    }

    @keyframes rowup {
      0% {
        -webkit-transform: translate3d(0, 0, 0);
        transform: translate3d(0, 0, 0);
      }

      5% {
        -webkit-transform: translate3d(0, -40px, 0);
        transform: translate3d(0, -40px, 0);
      }

      33% {
        -webkit-transform: translate3d(0, -40px, 0);
        transform: translate3d(0, -40px, 0);
      }

      38% {
        -webkit-transform: translate3d(0, -80px, 0);
        transform: translate3d(0, -80px, 0);
      }

      66% {
        -webkit-transform: translate3d(0, -80px, 0);
        transform: translate3d(0, -80px, 0);
      }

      71% {
        -webkit-transform: translate3d(0, -120px, 0);
        transform: translate3d(0, -120px, 0);
      }

      100% {
        -webkit-transform: translate3d(0, -120px, 0);
        transform: translate3d(0, -120px, 0);
      }
    }

    a {
      color: #fff;
      line-height: 22px;
      padding: 9px 0;
      font-weight: 400;
      // height: 40px;
      display: block;
      text-align: center;
      box-sizing: border-box;
      text-decoration: none;
    }

    a .link {
      position: relative;
      top: -1px;
      font-family: Poppins;
      box-sizing: border-box;
      font-weight: normal;
      font-size: 12px;
      line-height: 18px;
      color: #a19cbb;
      background: transparent;
      border-radius: 23px;
      padding: 0 8px;
      margin-left: 10px;
      display: inline-block;
      height: 20px;
      vertical-align: top;
      margin-top: 2px;
      font-weight: 500;
      border: 1px solid #a29cbc;
      &:hover {
        border-color: #c1a0ff;
        color: #c1a0ff;
      }
    }
  }
  .header-notice-icon {
    position: relative;
    top: -1px;
    display: inline-block;
    margin-right: 10px;
    width: 14px;
    height: 14px;
  }
}

