@import '../../../assets/css//var.scss';
@import '../../../assets/css/theme';
@import '../../../assets/css/mixins';

.dark-table-container {
  border-radius: 20px;
  overflow: hidden;
  @include mobile-only {
    border-radius: 6px;
  }
  // @include lightThemeOnly {
  //   .ant-table-header {
  //     .ant-table-cell {
  //       border-bottom: none;
  //     }
  //   }
  //   .ant-table-body {
  //     border: 1px solid #e5e5e5;
  //     border-top: none;
  //     border-radius: 0 0 20px 20px;
  //   }
  // }
}
.dark-table-container.ant-table-wrapper {
  .ant-table-thead > tr > th::before {
    display: none;
  }

  .ant-table-placeholder {
    color: $color-text-default-5 !important;
  }
  @include lightThemeOnly {
    .ant-table-thead th.ant-table-column-has-sorters:hover {
      background: #eae2ff;
    }
  }

  .ant-table-container table > thead {
    & > tr {
      &:first-child {
        & > *:first-child {
          border-top-left-radius: 0;
        }
        & > *:last-child {
          border-top-right-radius: 0;
        }
      }
    }
  }

  td.ant-table-column-sort {
    background: transparent;
  }

  .ant-table-pagination.ant-pagination {
    margin-top: 0;
    margin-bottom: 0;
    border-top: none;
    padding: 30px;
  }

  .ant-table-pagination {
    background-color: $color-table-bg;
    // border-radius: 0 0 20px 20px;
  }

  .ant-table-pagination.ant-pagination {
    & > .ant-pagination-item {
      margin-left: 8px;
    }

    .ant-pagination-item-1 {
      margin-left: 0;
    }
  }

  .ant-pagination-item-active a {
    color: #fff;
  }

  .ant-pagination.ant-table-pagination .ant-pagination-item {
    border: 1px solid $color-border-pagination !important;
    color: $color-border-pagination !important;
    overflow: hidden;
  }

  .ant-pagination-item a {
    color: $color-border-pagination;
  }

  .ant-pagination-item-active {
    background: #6d00f4;

    border-color: rgba(109, 0, 244, 1);
    a {
      color: rgba(255, 255, 255, 1);
      &:hover {
        color: rgba(255, 255, 255, 1);
      }
    }
  }

  .ant-pagination-jump-next,
  .ant-pagination-jump-prev {
    min-width: 24px;
    margin-left: 8px;
    border-radius: 5px;
    border: 1px solid $color-border-pagination;
    color: $color-border-pagination;
    .ant-pagination-item-link {
      color: rgba($color-text-default, 0.5);
      .ant-pagination-item-ellipsis {
        font-size: 12px;
        color: rgba($color-text-default, 0.5);
      }
      .ant-pagination-item-container {
        .ant-pagination-item-link-icon {
          margin-left: 0;
          color: rgba($color-text-default, 0.5);
        }
      }
    }
  }
  .ant-pagination .ant-pagination-disabled {
    opacity: 0.25;
  }
  .ant-table-column-sorters {
    justify-content: flex-start;
    .ant-table-column-title {
      flex: none;
      //color: $color-table-column-title;
    }
  }
}

.scanTableRender {
  .ant-table-pagination {
    background-color: #1b0d31 !important;
    // border-radius: 0 0 20px 20px;
  }
}
