@import 'theme';
@import 'mixins';
.list-update-modal {
  .title {
    font-size: 20px;
    font-family: Helvetica-Bold, Helvetica;
    font-weight: bold;
    color: $color-text-default;
    line-height: 30px;
    @include mobile-only {
      font-size: 16px;
    }
  }

  .list-update-footer {
    display: flex;
    justify-content: space-between;
    button {
      width: 47%;
    }
    margin-top: 30px;
  }
  .list-force-update-footer {
    justify-content: center;
    button {
      width: 100%;
    }
  }

  .detail {
    margin-top: 20px;
    max-height: 220px;
    overflow-y: auto;

    li {
      font-size: 16px;
      font-family: PingFangSC-Semibold, PingFang SC;
      font-weight: 600;
      color: $color-text-default;
      line-height: 22px;

      .content {
        font-size: 16px;
        font-family: PingFangSC-Regular, PingFang SC;
        font-weight: 400;
        color: $color-text-default-7;
        line-height: 22px;
        margin-bottom: 10px;
      }
      a {
        color: $color-text-default;
      }
    }
  }
}
